import { fromBinary } from "@bufbuild/protobuf";
import { GiftImportedEventSchema } from "@giftduels/protobuf-js/gen/giftduels/gift/v1/events_pb";
import type { ConsumerHandler } from "@/amqp/consumer";
import { NotificationService } from "@/services/notification";

export class GiftImportedHandler {
	constructor(private notification: NotificationService) {}

	public readonly handle: ConsumerHandler = async (raw, _props, ctrl) => {
		const evt = fromBinary(GiftImportedEventSchema, raw);

		await this.notification.sendGiftImportedNotification(
			Number(evt.ownerTelegramId?.value),
			{
				giftName: evt.title,
				slug: evt.slug,
			},
		);

		// 3) Подтверждаем очередь
		return ctrl.ack();
	};
}
